<template>
    <transition name="slide">
        <div class="add-song" v-show="showFlag" @click.stop>
            <div class="header">
                <h1 class="title"></h1>
                <div class="close" @click="hide">
                    <i class="icon-close"></i>
                </div>
            </div>
            <div class="search-box-wrapper">
                <PSearchBox ref="searchBox" @query="onQueryChange" placeholder=""></PSearchBox>
            </div>
            <div class="shortcut" v-show="!query">
                <PSwitches :switches="switches"
                           :currentIndex="currentIndex"
                           @switch="switchItem"></PSwitches>
                <div class="list-wrapper">
                    <PScroll ref="songList"
                             class="list-scroll"
                             :data="playHistory"
                             :refreshDelay="refreshDelay"
                             v-if="currentIndex===0">
                        <div class="list-inner">
                            <PSongList :songs="playHistory" @select="selectSong"></PSongList>
                        </div>
                    </PScroll>
                    <PScroll class="list-scroll"
                             ref="searchList"
                             :data="searchHistory"
                             v-if="currentIndex===1">
                        <div class="list-inner">
                            <PSearchList :searches="searchHistory"
                                         @delete="deleteOne"
                                         @select="addQuery"
                            ></PSearchList>
                        </div>
                    </PScroll>
                </div>
            </div>
            <div class="search-result" v-show="query">
                <PSuggest :query="query"
                          :showSinger="showSinger"
                          @select="selectSuggest"
                          @listScroll="blurInput"
                ></PSuggest>
            </div>
            <PTopTip ref="topTip">
                <div class="tip-title">
                    <i class="icon-ok"></i>
                    <span class="text">1</span>
                </div>
            </PTopTip>
        </div>
    </transition>
</template>

<script>
    import PSearchBox from '@/components/base/SearchBox'
    import PSuggest from '@/components/search/Suggest'
    import PSwitches from '@/components/base/Switches'
    import PScroll from '@/components/base/Scroll'
    import PSongList from '@/components/base/SongList'
    import PSearchList from '@/components/base/SearchList'
    import PTopTip from '@/components/base/TopTip'
    import Song from 'js/song'
    import {mapGetters,mapActions} from 'vuex'
    import {searchMixin} from "js/mixin";
    export default {
        mixins:[searchMixin],
        name: "AddSong",
        components:{
            PSearchBox,
            PSuggest,
            PSwitches,
            PScroll,
            PSongList,
            PSearchList,
            PTopTip
        },
        data(){
            return {
                showFlag:false,
                showSinger:false,
                switches:[
                    {name:''},
                    {name:''}
                ],
                currentIndex:0
            }
        },
       computed:{
           ...mapGetters([
               'playHistory'
           ])
       },
       methods:{
           show(){
               this.showFlag=true
               setTimeout(()=>{
                   if(this.currentIndex===0){
                       this.$refs.songList.refresh()
                   }else{
                       this.$refs.searchList.refresh()
                   }
               },20)
           },
           hide(){
               this.showFlag=false
           },
           selectSuggest(){
               this.saveSearch();
               this.showTopTip()
           },
           switchItem(index){
               this.currentIndex=index
           },
           selectSong(item,index){
               if(index!==0){
                   this.insertSong(new Song(item));
                   this.showTopTip()
               }
           },
           showTopTip(){
             this.$refs.topTip.show()
           },
           ...mapActions([
               'insertSong'
           ])
       }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~stylus/variable"
    @import "~stylus/mixin"

    .add-song
        position: fixed
        top: 0
        bottom: 0
        width: 100%
        z-index: 200
        background: $color-background
        &.slide-enter-active, &.slide-leave-active
            transition: all 0.3s
        &.slide-enter, &.slide-leave-to
            transform: translate3d(100%, 0, 0)
        .header
            position: relative
            height: 44px
            text-align: center
            .title
                line-height: 44px
                font-size: $font-size-large
                color: $color-text
            .close
                position: absolute
                top: 0
                right: 8px
                .icon-close
                    display: block
                    padding: 12px
                    font-size: 20px
                    color: $color-theme

        .search-box-wrapper
            margin: 20px
        .shortcut
            .list-wrapper
                position: absolute
                top: 165px
                bottom: 0
                width: 100%
                .list-scroll
                    height: 100%
                    overflow: hidden
                    .list-inner
                        padding: 20px 30px
        .search-result
            position: fixed
            top: 124px
            bottom: 0
            width: 100%
        .tip-title
            text-align: center
            padding: 18px 0
            font-size: 0
            .icon-ok
                font-size: $font-size-medium
                color: $color-theme
                margin-right: 4px
            .text
                font-size: $font-size-medium
                color: $color-text
</style>


