import {playMode} from 'js/config'
import {loadSearch,loadPlay,loadFavorite} from "js/catche";

const state={
    singer:{},
    playing:false, // 
    fullScreen:false, // 
    playlist:[],// 
    sequenceList:[],// 
    mode:playMode.sequence, // 
    currentIndex:-1, // ,
    disc:{},
    topList:{},// 
    searchHistory:loadSearch(),
    playHistory:loadPlay(),
    favoriteList:loadFavorite(),// 
};
export default state


